                    <div>Thi gian cp nht: {{modifiedDate}}</div>
                </div>
            </div>
            <div class="course-detail-area">
                <div class="course-detail-tabs my-3">
                    <div>
                        <button class="active me-2" @click="() => switchTab(1)">Gii thiu</button>
                        <button @click="() => switchTab(3)" class="lesson">Bi hc</button>
                        <button class="me-2" @click="() => switchTab(2)">Ti liu</button>
                    </div>
                    <span class="indicator"></span>
                </div>
                <div class="course-about mt-4" v-show="switchToggle == 1">
                    <div class="course-intro">
                        <p>Kho hc vi 100% ging vin ngi bn ng, n t Topica Native</p>
                        <p>Hc vin thng xuyn c kim tra, nh gi qua cc bi kim tra trong v sau mi bi hc</p>
